/* FRA Claims Dashboard Styles */
.fra-claims-container {
    min-height: 100vh;
    background: linear-gradient(135deg, #f5f5dc 0%, #e8f5e8 100%);
    font-family: 'Inter', sans-serif;
}

/* Header */
.claims-header {
    background: linear-gradient(135deg, #1a4d3a 0%, #2d6b4f 100%);
    color: white;
    padding: 4rem 2rem 3rem;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.claims-header::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(circle at 30% 70%, rgba(125, 180, 108, 0.3) 0%, transparent 50%),
                radial-gradient(circle at 70% 30%, rgba(74, 128, 100, 0.3) 0%, transparent 50%);
    animation: shimmer 8s ease-in-out infinite;
}

@keyframes shimmer {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.7; }
}

.header-content {
    position: relative;
    z-index: 2;
    max-width: 800px;
    margin: 0 auto;
}

.claims-header h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    font-weight: 700;
}

.claims-header p {
    font-size: 1.2rem;
    opacity: 0.9;
}

/* Statistics Overview */
.stats-overview {
    padding: 2rem 1rem;
    background: white;
}

.stats-container {
    max-width: 1000px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
}

.stat-card {
    background: white;
    border-radius: 12px;
    padding: 1.5rem;
    box-shadow: 0 6px 25px rgba(26, 77, 58, 0.1);
    border: 2px solid transparent;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(135deg, #1a4d3a 0%, #2d6b4f 100%);
}

.stat-card.approved::before {
    background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
}

.stat-card.pending::before {
    background: linear-gradient(135deg, #ffc107 0%, #fd7e14 100%);
}

.stat-card.rejected::before {
    background: linear-gradient(135deg, #dc3545 0%, #e83e8c 100%);
}

.stat-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 60px rgba(26, 77, 58, 0.2);
    border-color: #7db46c;
}

.stat-card .stat-icon {
    font-size: 2rem;
    margin-bottom: 0.75rem;
    display: block;
}

.stat-content h3 {
    color: #2c3e26;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.stat-number {
    font-size: 1.8rem;
    font-weight: 700;
    color: #1a4d3a;
    margin-bottom: 0.25rem;
}

.stat-percentage {
    font-size: 0.85rem;
    color: #6b7c5e;
    font-weight: 500;
}

/* District Breakdown */
.district-breakdown {
    padding: 4rem 2rem;
    background: linear-gradient(135deg, #f5f5dc 0%, #e8f5e8 100%);
}

.section-container {
    max-width: 1200px;
    margin: 0 auto;
}

.district-breakdown h2 {
    text-align: center;
    font-size: 2.5rem;
    color: #1a4d3a;
    margin-bottom: 3rem;
    font-weight: 700;
}

.districts-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 2rem;
}

.district-card {
    background: white;
    border-radius: 16px;
    padding: 2rem;
    box-shadow: 0 8px 30px rgba(26, 77, 58, 0.1);
    transition: all 0.3s ease;
    border: 1px solid rgba(125, 180, 108, 0.2);
}

.district-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 50px rgba(26, 77, 58, 0.15);
}

.district-card h3 {
    color: #1a4d3a;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    font-weight: 600;
    border-bottom: 2px solid #7db46c;
    padding-bottom: 0.5rem;
}

.district-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    font-size: 1.1rem;
}

.district-total .label {
    color: #6b7c5e;
    font-weight: 500;
}

.district-total .value {
    color: #1a4d3a;
    font-weight: 700;
    font-size: 1.3rem;
}

.status-breakdown {
    margin-bottom: 1.5rem;
}

.status-item {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
    font-size: 0.9rem;
}

.status-item .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 0.5rem;
}

.status-item.approved .dot {
    background: #28a745;
}

.status-item.pending .dot {
    background: #ffc107;
}

.status-item.rejected .dot {
    background: #dc3545;
}

.progress-bar {
    height: 8px;
    background: #e9ecef;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
}

.progress-approved {
    background: #28a745;
    transition: width 0.8s ease;
}

.progress-pending {
    background: #ffc107;
    transition: width 0.8s ease;
}

.progress-rejected {
    background: #dc3545;
    transition: width 0.8s ease;
}

/* Navigation Section */
.navigation-section {
    padding: 3rem 2rem;
    background: white;
    text-align: center;
}

.nav-container {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    gap: 2rem;
    justify-content: center;
}

.back-btn, .map-btn {
    padding: 1rem 2rem;
    border: none;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
}

.back-btn {
    background: linear-gradient(135deg, #6c757d 0%, #495057 100%);
    color: white;
}

.map-btn {
    background: linear-gradient(135deg, #1a4d3a 0%, #2d6b4f 100%);
    color: white;
}

.back-btn:hover, .map-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
    .claims-header h1 {
        font-size: 2rem;
    }
    
    .claims-header p {
        font-size: 1rem;
    }
    
    .stats-overview {
        padding: 1.5rem 1rem;
    }
    
    .stats-container {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
        max-width: 500px;
    }
    
    .stat-card {
        padding: 1rem;
    }
    
    .stat-card .stat-icon {
        font-size: 1.5rem;
        margin-bottom: 0.5rem;
    }
    
    .stat-number {
        font-size: 1.4rem;
    }
    
    .stat-content h3 {
        font-size: 0.8rem;
    }
    
    .stat-percentage {
        font-size: 0.75rem;
    }
    
    .districts-grid {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    
    .nav-container {
        flex-direction: column;
        gap: 1rem;
    }
    
    .district-breakdown h2 {
        font-size: 2rem;
    }
}

@media (max-width: 480px) {
    .stats-container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
}
